"use client"

import { Button } from "@/components/ui/button"
import {
    BarChart3,
    Clock,
    Home,
    LayoutDashboard,
    Radio,
    Settings,
    Users,
} from "lucide-react"
import Image from "next/image"
import Link from "next/link"
import { usePathname, useRouter } from "next/navigation"
import { useEffect, useState } from "react"

interface Admin {
  id: number
  email: string
  name?: string
  first_name?: string
  last_name?: string
  [key: string]: any
}

export default function AdminLayout({ children }: { children: React.ReactNode }) {
  const router = useRouter()
  const pathname = usePathname()
  const [admin, setAdmin] = useState<Admin | null>(null)

  useEffect(() => {
    const adminData = localStorage.getItem("admin")
    if (!adminData) {
      router.push("/")
      return
    }
    setAdmin(JSON.parse(adminData))
  }, [router])

  const handleLogout = () => {
    localStorage.removeItem("admin")
    router.push("/")
  }

  if (!admin) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-amber-50 to-orange-50">
        <div className="animate-spin rounded-full h-16 w-16 border-b-2 border-red-800"></div>
      </div>
    )
  }

  const navItems = [
    {
      href: "/admin",
      label: "Dashboard",
      icon: LayoutDashboard,
    },
    {
      href: "/admin/live-attendance",
      label: "Live Attendance",
      icon: Clock,
    },
    {
      href: "/admin/attendance-reports",
      label: "Attendance Reports",
      icon: BarChart3,
    },
    {
      href: "/admin/teacher-attendance",
      label: "Teacher Attendance",
      icon: Users,
    },
    {
      href: "/admin/rfid-display",
      label: "RFID Display",
      icon: Radio,
    },
    {
      href: "/admin/students",
      label: "Student Management",
      icon: Users,
    },
    {
      href: "/admin/settings",
      label: "System Settings",
      icon: Settings,
    },
  ]

  return (
    <div className="min-h-screen bg-gradient-to-br from-amber-50 to-orange-50">
      {/* Header */}
      <header className="bg-white shadow-md border-b-4 border-red-800 sticky top-0 z-40">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-4">
              <Image
                src="/logo.png"
                alt="Sto Niño de Praga Academy Logo"
                width={50}
                height={50}
                className="rounded-full"
              />
              <div>
                <h1 className="text-xl font-bold text-red-800">Admin Portal</h1>
                <p className="text-sm text-gray-600">Sto Niño de Praga Academy</p>
              </div>
            </div>
            <div className="flex items-center space-x-4">
              <Link href="/">
                <Button
                  variant="outline"
                  className="border-red-800 text-red-800 hover:bg-red-800 hover:text-white bg-transparent"
                >
                  <Home className="w-4 h-4 mr-2" />
                  Home
                </Button>
              </Link>
              <div className="text-right">
                <p className="font-medium text-red-800">
                  {admin.name || admin.first_name || admin.email || "Admin User"}
                </p>
                <p className="text-sm text-gray-600">System Administrator</p>
              </div>
              <Button
                onClick={handleLogout}
                variant="outline"
                className="border-red-800 text-red-800 hover:bg-red-800 hover:text-white bg-transparent"
              >
                Logout
              </Button>
            </div>
          </div>
        </div>
      </header>

      {/* Main Content with Sidebar */}
      <div className="flex">
       
        <main className="flex-1 p-8">{children}</main>
      </div>
    </div>
  )
}
